
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default {
name: "msg",
components: {
ButtonTab,
ButtonTabItem,
XButton,
Cell,
Group,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
data() {
return {
selectedTabIndex: 0,
hasReadMessages: [],
hasnotReadMessages: []
};
},
created: function() {

var app = this;
this.$http
.get("messages", {
params: {
accesstoken: this.$store.state.accesstoken
}
})
.then(function(response) {
if (response.data.success) {
console.log(response.data.data);
app.hasReadMessages = response.data.data.has_read_messages;
app.hasnotReadMessages = response.data.data.hasnot_read_messages;
}
});
},
methods: {

markRead: function(msgid) {

var app=this;
this.$http
.post("/message/mark_one/" + msgid, {
accesstoken: this.$store.state.accesstoken
})
.then(function(response) {
console.log('已经标记为已读');


app.$router.go(0);
});
},

markAll:function(){
var app=this;
this.$http
.post("/message/mark_all", {
accesstoken: this.$store.state.accesstoken
})
.then(function(response) {
console.log('已经全部标记为已读');


app.$router.go(0);
});
}
}
};



