// vuex状态管理

import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';


//全局注册
Vue.use(Vuex);

//创建store对象
const store=new Vuex.Store({
//状态数据
state:{
    topics:[
        {name:'all',desc:'全部'},
        {name:'good',desc:'精华'},
        {name:'share',desc:'分享'},
        {name:'ask',desc:'问答'},
        {name:'job',desc:'招聘'},
        {name:'dev',desc:'客户端测试'},


      ],
      //actionsheet的菜单
      //用户未登陆时显示登录
      unLoginedmoreMenus:{
        login:'登录',
       

      },
      //用户登录时显示
      loginedMoreMenus:{
        create:'发表新话题',
        center:'个人中心',
        msg:'消息通知',
        logout:'登出'

      },
      //用户登录状态
      isLoginde:false,
      loginname:'',
      accesstoken:'',
      //登录id
      author_id:'',
      //actionsheet显示与否变量
      isShowMoreMenus:false
},
//状态数据的计算属性
getters:{
    moreMenus:function(state){
        if(state.isLoginde){
          //用户已登录
          return state.loginedMoreMenus;
        }
        return state.unLoginedmoreMenus;
      },
      getTopicDescByTab:function(state){
        return function(tab){
          for(var i=0;i<state.topics.length;i++){
            if(state.topics[i].name==tab){
              return state.topics[i].desc;
            }
          }
        }
      }
},
//修改同步数据的函数
mutations,
//异步数据
actions
})

//对外输出
export default store;


