
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import {mapState,mapGetters,mapMutations} from 'vuex'
import VmBackTop from 'vue-multiple-back-top';
export default {
name: 'app',
components:{
Tab,
TabItem,
ButtonTab,
ButtonTabItem ,
XHeader,
Actionsheet,
VmBackTop,
Badge
},
data:function(){
return{
badgeText:''
}
},
computed:{



...mapState(['topics','unLoginedmoreMenus','loginedMoreMenus','isLoginde']),
...mapGetters(['moreMenus']),
isShowMoreMenus:{
set:function(nval){

this.$store.commit('mutationIsShowMoreMenus',nval)
},
get:function(){
return this.$store.state.isShowMoreMenus
}
},

},
methods:{
...mapMutations(['mutationLogin','mutationLoginname','mutationAccessToken','mutationAuthorId']),




showMoreMenus(){
this.isShowMoreMenus=true;

},

handleClickMoreMenus(menukey,menutem){



this.$router.push('/'+menukey);
},

getUnreadMsgCount:function(){
var app =this;
this.$http.get('/message/count',{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(response){

app.badgeText=response.data.data;
})
}
},

created:function(){
var loginStatus=window.localStorage.getItem('loginStatus');
var loginName=window.localStorage.getItem('loginname');
var loginAccesstoken=window.localStorage.getItem('accesstoken');
var loginAuthorId=window.localStorage.getItem('author_id')

if(loginStatus){

this.mutationLogin(true);

this.mutationLoginname(loginName);

this.mutationAccessToken(loginAccesstoken);

this.mutationAuthorId(loginAuthorId);

var app=this;
window.setInterval(function(){
app.getUnreadMsgCount();
},1000*30)
};
}
}





